.header_container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
/* ========
header top
========== */
.header_top_container {
  display: flex;
  position: fixed;
  color: white;
  height: 70px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  font-family: Exo2;
  z-index: 999999;
}

.nav_container {
  margin-right: 1rem;
  list-style: none;
  display: flex;
  font-size: 20px;
}

.nav_container li {
  margin: 0 1rem;
}
/* ========
clock
========== */
.header_clock {
  margin-left: 1.5rem;
  font-size: 20px;
  width: 80px;
  text-align: center;
}
/* ========
roller
========= */
.header_center {
  margin-left: 200px;
}
.header_wrap {
  position: relative;
  width: 200px;
  height: 40px;
  overflow-x: hidden;
  overflow-y: hidden;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}
.header_roller {
  position: absolute;
  white-space: nowrap;
  will-change: transform;
  animation: marquee 20s linear infinite;
}
.header_inner_name {
  margin-top: 0.55rem;
}
@keyframes marquee {
  from {
    transform: translateX(25%);
  }
  to {
    transform: translateX(-100%);
  }
}
/* ========
switch btn
========= */
.change_div_container {
  border: 1px solid white;
  border-radius: 15px 15px 15px 15px;
  width: 15rem;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.change_btns {
  border: none;
  cursor: pointer;
  font-family: Exo2;
  background-color: #00ff0000;
  text-transform: uppercase;
  width: 7.5rem;
  height: 2.15rem;
  z-index: 2;
}
.change_btn {
  color: #fff;
}
.change_btn_1 {
  color: #3e9d89;
}
.active_box {
  position: absolute;
  width: 7.5rem;
  height: 2.15rem;
  background-color: #fff;
  z-index: 1;
  border-radius: 15px 0 0 15px;
}
