/* ========
header main 
========== */
.header_main_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 95vh;
  position: fixed;
}
.title {
  position: relative;
  width: 100vw;
  height: 50vh;
  color: #fff;
  margin-top: 5rem;
}
.title_1,
.title_2,
.title_3 {
  position: absolute;
  font-weight: lighter;
  font-family: Yasashii;
  font-size: 10rem;
}
.title_1 {
  left: 450px;
}
.title_2 {
  top: 130px;
  left: 600px;
}
.title_3 {
  top: 260px;
  right: 400px;
}
.title_4 {
  position: absolute;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 25px;
  font-weight: 500;
  top: 410px;
  right: 480px;
}
.fade_in_1 {
  animation: fade_in_1 700ms;
  -moz-animation: fade_in_1 700ms;
  -webkit-animation: fade_in_1 700ms;
  -o-animation: fade_in_1 700ms;
  animation-fill-mode: forwards;
}
.fade_out_1 {
  animation: fade_out_1 300ms;
  -moz-animation: fade_out_1 300ms;
  -webkit-animation: fade_out_1 300ms;
  -o-animation: fade_out_1 300ms;
  animation-fill-mode: forwards;
}
@keyframes fade_in_1 {
  from {
    transform: translate(0rem, 0px);
    opacity: 0;
  }
  to {
    transform: translate(1rem, 0px);
    opacity: 1;
  }
}
@keyframes fade_out_1 {
  from {
    transform: translate(1rem, 0px);
    opacity: 1;
  }
  to {
    transform: translate(0rem, 0rem);
    opacity: 0;
  }
}
.fade_in_2 {
  animation: fade_in_2 700ms;
  -moz-animation: fade_in_2 700ms;
  -webkit-animation: fade_in_2 700ms;
  -o-animation: fade_in_2 700ms;
  animation-fill-mode: forwards;
}
.fade_out_2 {
  animation: fade_out_2 300ms;
  -moz-animation: fade_out_2 300ms;
  -webkit-animation: fade_out_2 300ms;
  -o-animation: fade_out_2 300ms;
  animation-fill-mode: forwards;
}

@keyframes fade_in_2 {
  from {
    transform: translate(0rem, 0px);
    opacity: 0;
  }
  to {
    transform: translate(-20rem, 0px);
    opacity: 1;
  }
}
@keyframes fade_out_2 {
  from {
    transform: translate(-20rem, 0px);
    opacity: 1;
  }
  to {
    transform: translate(0rem, 0rem);
    opacity: 0;
  }
}
.fade_in_3 {
  animation: fade_in_3 700ms;
  -moz-animation: fade_in_3 700ms;
  -webkit-animation: fade_in_3 700ms;
  -o-animation: fade_in_3 700ms;
  animation-fill-mode: forwards;
}
.fade_out_3 {
  animation: fade_out_3 300ms;
  -moz-animation: fade_out_3 300ms;
  -webkit-animation: fade_out_3 300ms;
  -o-animation: fade_out_3 300ms;
  animation-fill-mode: forwards;
}

@keyframes fade_in_3 {
  from {
