    opacity: 1;
  }
}
@keyframes fade_out_3 {
  from {
    transform: translate(5rem, 0px);
    opacity: 1;
  }
  to {
    transform: translate(-10rem, 0px);
    opacity: 0;
  }
}
.fade_in_4 {
  animation: fade_in_4 700ms;
  -moz-animation: fade_in_4 700ms;
  -webkit-animation: fade_in_4 700ms;
  -o-animation: fade_in_4 700ms;
  animation-fill-mode: forwards;
}
.fade_out_4 {
  animation: fade_out_4;
  -moz-animation: fade_out_4;
  -webkit-animation: fade_out_4;
  -o-animation: fade_out_4;
  animation-fill-mode: forwards;
}

@keyframes fade_in_4 {
  from {
    transform: translate(0rem, 0px);
    opacity: 0;
  }
  to {
    transform: translate(15rem, 0px);
    opacity: 1;
  }
}
@keyframes fade_out_4 {
  from {
    opacity: 1;
    transform: translate(15rem, 0px);
  }
  to {
    opacity: 0;
    transform: translate(15rem, 0px);
  }
}
