.div_hide {
  display: none;
}
.project_container {
  color: white;
  height: 100vh;
  background-color: #3e9d89;
  font-family: NanumSquareRound;
  padding: 5rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.project_text {
  width: 35vw;
}
.project_text h2 {
  font-size: 5rem;
}
.project_text h3 {
  font-size: 2rem;
  margin-top: 3rem;
}
.project_text p {
  font-size: 1.3rem;
  line-height: 2rem;
}

.project_showroom {
  display: flex;
  width: 65vw;
  flex-wrap: wrap;
  justify-content: space-around;
}
.hov_box {
  width: 100%;
  height: 330px;
  border-top: 2px solid #fff;
  width: 30rem;
  position: relative;
}
.hov_box:nth-child(1),
.hov_box:nth-child(2) {
  margin-bottom: 5rem;
}
.gif {
  position: absolute;
  background-image: url("../../img/20221205182338_Trim.gif");
  background-size: 500px;
  width: 100%;
  height: 280px;
  opacity: 0;
  box-shadow: 0px 0px 30px 10px black;
}
.gif_stop {
  background-image: url("../../img/airbnb.jpg");
  background-size: 500px;
  width: 100%;
  height: 280px;
}
.hov_box_gif:hover .gif {
  animation: hover_gif 100ms, hover_opacity_1 300ms 200ms;
  animation-fill-mode: forwards;
}
.hov_box_gif:not(:hover) .gif {
  animation: hover_gif_stop 100ms 500ms, hover_opacity_0 300ms;
}
@keyframes hover_gif {
  0% {
    background-size: 500px;
    height: 280px;
    z-index: 0;
  }
  100% {
    background-size: 800px;
    left: -170px;
    top: -50px;
    width: 800px;
    height: 450px;
    z-index: 3;
  }
}
@keyframes hover_gif_stop {
  0% {
    background-size: 800px;
    left: -170px;
    top: -50px;
    width: 800px;
    height: 450px;
    z-index: 3;
  }
  100% {
    background-size: 500px;
    height: 280px;
    z-index: 0;
  }
}

