.div_hide {
  opacity: 0;
}
.linkbar_container {
  height: 5vh;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 15px;
  font-family: Exo2;
  bottom: 0;
  width: 100%;
}
.linkbar_links {
  margin-left: 2rem;
}
.linkbar_list {
  display: flex;
  list-style: none;
}
.linkbar_item {
  margin-right: 2rem;
}
.linkbar_item a:link {
  text-decoration: none;
}
.linkbar_item a:hover {
  text-decoration: none;
}
.linkbar_item a:visited {
  text-decoration: none;
  color: #fff;
}
.linkbar_item a:active {
  text-decoration: none;
}
.linkbar_guide {
  margin-right: 2rem;
  position: relative;
}
.linkbar_guide_text {
  margin-right: 1rem;
}
.linkbar_guide_arrow {
  animation: motion 0.7s linear 0s infinite alternate;
  position: absolute;
}
@keyframes motion {
  0% {
    bottom: 5px;
  }
  100% {
    bottom: 25px;
  }
}
