.about_container {
  height: 100vh;
  color: #fff;
  font-family: NanumSquareRound;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.about_container h4 {
  font-size: 3rem;
  margin: 30px 0;
}
.about_container p {
  margin-top: 10px;
  margin-bottom: 50px;
  font-weight: 100;
  line-height: 2.5rem;
  font-size: 20px;
}
.about_text_left {
  padding-left: 2rem;
  margin-bottom: 200px;
}
.about_text_right {
  padding-right: 2rem;
  margin-top: 500px;
}
